<template>
  <div class="tk-console">
    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">Logs</h6>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <!-- <base-button
            size="sm"
            type="white"
            class="text-main"
            >Account Type</base-button
          > -->
        </div>
      </div>
    </base-header>

    <div class="container-fluid mt--6">
      <div class="card-wrapper">
        <div class="card">
          <div class="card-body">
            <div class="row tk-px-margin-bottom-px20">
              <div class="col-6">
                <!-- <h5 class="h3 mb-0">Links</h5> -->
                <span>From</span>
                <flat-picker
                  :config="{ allowInput: true }"
                  class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px8 tk-px-height-px36"
                  v-model="from"
                >
                </flat-picker>
                <span>&nbsp;&nbsp;- To</span>
                <flat-picker
                  :config="{ allowInput: true }"
                  class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px8 tk-px-height-px36"
                  v-model="to"
                >
                </flat-picker>
                <span
                  class="btn btn-sm btn-neutral tk-px-margin-left-px16"
                  :class="{
                    'tk-bg-color-gray-400 tk-border-color-gray-400 tk-font-color-white': !applyIsEnable,
                    'tk-font-color-main': applyIsEnable,
                  }"
                  @click.prevent="initLogs"
                  >Apply</span
                >
                <!-- <span
                  class="btn btn-sm btn-neutral tk-px-margin-left-px8"
                  :class="{
                    'tk-bg-color-gray-400 tk-border-color-gray-400 tk-font-color-white': !applyIsEnable,
                    'tk-font-color-main': applyIsEnable,
                  }"
                  @click.prevent="initLogs"
                  >All dates</span
                > -->
              </div>
              <div class="col-6 text-right">
                <el-select v-model="currentAction" style="width: 300px">
                  <el-option
                    v-for="item in actions"
                    :key="item"
                    :label="item"
                    :value="item"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
            <el-table
              class="table-responsive align-items-center table-flusht"
              :height="tableHeight"
              header-row-class-name="thead-light"
              :data="displayLogs"
            >
              <el-table-column label="Log ID" prop="id" width="90">
                <template v-slot="{ row }">
                  <span>{{ shortId(row.id) }}</span>
                  <span
                    v-clipboard:copy="row.id"
                    v-clipboard:error="onCopyError"
                    v-clipboard:success="onCopySuccess"
                    class="table-action"
                    data-toggle="tooltip"
                    style="margin-left: 8px !important"
                  >
                    <i class="fas fa-copy"></i>
                  </span>
                </template>
              </el-table-column>
              <el-table-column
                label="Page Name"
                prop="pageName"
                width="120"
              ></el-table-column>
              <el-table-column
                label="Name"
                prop="name"
                width="100"
              ></el-table-column>
              <el-table-column
                label="Email"
                prop="email"
                width="120"
              ></el-table-column>
              <el-table-column label="Date" prop="date" width="80">
                <template v-slot="{ row }">
                  {{ formatMMDDYY(row.createTime) }}
                </template>
              </el-table-column>
              <el-table-column label="Time" prop="time" width="80">
                <template v-slot="{ row }">
                  {{ formatHHMMAP(row.createTime) }}
                </template>
              </el-table-column>
              <el-table-column
                label="Action"
                prop="action"
                width="80"
              ></el-table-column>
              <el-table-column
                label="Document path"
                prop="documentPath"
                min-width="128"
              ></el-table-column>
              <el-table-column
                label="Parameters"
                prop="parameters"
                min-width="100"
              >
                <template v-slot="{row}">
                  <div style="max-height: 100px; overflow: auto">{{row.parameters}}</div>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "flatpickr/dist/flatpickr.css";
import $ from "jquery";
import query from "@/services/query";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
  Select,
  Option,
} from "element-ui";
export default {
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    [Select.name]: Select,
    [Option.name]: Option,
    flatPicker: () => import("vue-flatpickr-component"),
  },
  data() {
    return {
      tableHeight: 0,
      actions: ["All actions", "Create", "Delete", "Update", "Query"],
      currentAction: "All actions",
      applyIsEnable: true,
      from: "",
      to: "",
      logs: [
        {
          id: "03w1TxgQovbcWsG8Uj6Z3QqZYRh1",
          pageName: "Logs",
          name: "Wayne Wang",
          email: "mailwpw@gmail.com",
          date: "4/23/2021",
          time: "12:07 AM",
          action: "Query",
          documentPath: "/logs",
          parameters: "",
        },
      ],
      displayLogs: [],
      consoleUser: null,
    };
  },
  watch: {
    currentAction(newValue) {
      this.initLogs();
    },
    from(newValue) {
      if (this.to.length > 0) {
        let ymdFrom = newValue.replace(/_/g, "/");
        let ymdTo = this.to.replace(/_/g, "/");
        if (new Date(ymdFrom).getTime() > new Date(ymdTo).getTime()) {
          this.from = this.to;
        }
      }
    },
    to(newValue) {
      if (this.from.length > 0) {
        let ymdFrom = this.from.replace(/_/g, "/");
        let ymdTo = newValue.replace(/_/g, "/");
        if (new Date(ymdFrom).getTime() > new Date(ymdTo).getTime()) {
          this.to = this.from;
        }
      }
    },
  },
  async created() {
    let management = await query.getAccountManagement();
    let accounts = management.accounts;
    let user = localStorage.getItem("auth_user");

    if (!this.$tools.isNull(user)) {
      user = JSON.parse(user);
      accounts.some((item) => {
        if (item.userId == user.uid) {
          this.consoleUser = item;
          return;
        }
      });
    }

    let d = new Date();
    let ymd = this.$tools.formatYYMMDD(d.getTime());
    this.tableHeight = document.documentElement.clientHeight - 220;
    this.from = this.to = this.$tools.formatYYMMDD(new Date().getTime());
    this.initLogs();

    //add log
    if (!this.$tools.isNull(this.consoleUser)) {
      await query.createLogs({
        id: this.$tools.generateId(),
        email: this.consoleUser.email,
        name: this.consoleUser.name,
        createTime: String(parseInt(new Date().getTime() / 1000)),
        action: "Query",
        documentPath: "/logs",
        parameters: "",
        pageName: "Logs",
      });
    }
  },
  methods: {
    formatMMDDYY(time) {
      return this.$tools.formatMMDDYY(time * 1000);
    },
    formatHHMMAP(time) {
      return this.$tools.formatHHMMAP(time * 1000);
    },
    async initLogs() {
      let from = parseInt(new Date(this.from).getTime() / 1000)
      let to = parseInt(new Date(this.to).getTime() / 1000)
      if (from == to) {
        to += 24 * 60 * 60
      }
      this.logs = await query.getLogs(String(from), String(to));
      this.displayLogs = [];
      if (this.actions.indexOf(this.currentAction) == 0) {
        this.displayLogs = this.logs;
      } else {
        this.logs.some((item) => {
          if (item.action == this.currentAction) {
            this.displayLogs.push(item);
          }
        });
      }
    },
    shortId(id) {
      let front = id.substring(0, 2);
      let back = id.substring(id.length - 2, id.length);
      return front + "..." + back;
    },
    onCopySuccess() {
      this.$notify({
        message: "Copy Success!",
        type: "success",
      });
    },
    onCopyError() {
      this.$notify({
        message: "Copy Failed!",
        type: "error",
      });
    },
  },
};
</script>

<style>
.tk-console .el-select .el-input .el-input__inner:focus {
  border-color: #71d9c2 !important;
}
.card .table td,
.card .table th,
.card .el-table td,
.card .el-table th {
  padding-left: 5px;
  padding-right: 5px;
}
</style>